import axios from 'axios';
import {Notification} from 'element-ui';

let base = '';
let header = {
    'content-type': 'application/json'
};

// axios请求基础配置
axios.defaults.headers.get['Content-Type'] = 'application/json';
// axios.defaults.timeout = 60000;
axios.defaults.baseURL = '';
axios.interceptors.request.use(config => { // 请求拦截器
    return config
}, error => {
    return Promise.reject(error)
})

axios.interceptors.response.use(response => { // 响应拦截器
    return response
}, error => {
    // 返回接口返回的错误信息
    return Promise.resolve(error.response)
})

let api = { //接口地址
    //公用接口----------------------------------------------------------------------
    login: '/login/ajax', // 登录
    logout: '/logout/ajax', // 登出
    logCode: 'sys/send_email', // 验证码获取
    changePwd: '/account/password/update', // 修改密码

    //直传-----
    imgUpload: '/ks3/image_upload', //图片直传
    musicSign: '/ks3/signature/music', //音乐文件获取签名
    videoSign: '/ks3/signature/video', //视频文件获取签名
    videoUpload: '/ks3/video_upload', //视频文件上传
    musicUpload: '/ks3/music_upload', //音乐文件上传
    lrcSign: '/ks3/signature/lines', // 台词文件获取签名
    lrcUpload: '/ks3/lrc_upload', // 直传台词文件
    zipUpload: '/ks3/signature/zip', // 直传Zip文件

    // 业务-------------------------------------------------------------------------

    //商品
    productList: '/shop/product/list',
    productSave: '/shop/product/save',
    productDetail: '/shop/product/detail',

    //商家/作者
    authorList:'/shop/product/listAuthor',
    authorPage:'/shop/product/listAuthorPage',
    authorSave:'/shop/product/saveAuthor',
    authorTypes:'/shop/product/listAuthorType',


    //热门推荐
    proRecList:'/shop/product/listSelected',
    proRecDelete:'/shop/product/delSelected',
    proRecTop:'/shop/product/topSelected',
    proRecSave:'/shop/product/saveSelected',
    proRecMove:'/shop/product/upSelected',
    proRecOffLine:'/shop/product/onSelected',

    //火热预售
    proPreList:'/shop/product/listPreorder',
    proPreSave:'/shop/product/savePreorder',
    proPreDelete:'/shop/product/delPreorder',
    proPreOffLine:'/shop/product/onPreorder',
    proPreMove:'/shop/product/upPreorder',
    proPreTop:'/shop/product/topPreorder',

    //订单管理
    orderList:'/shop/order/list',
    orderClose:'/shop/order/close',
    orderConfirm:'/shop/order/confirm',
    orderLog:'/shop/order/log',
    orderListLog:'/shop/order/listLog',
    orderUpdate:'/shop/order/update',
    orderSend:'/shop/order/send',
    expressList:'/shop/order/listExpress',
    orderExport:'/shop/order/export',

    //尺寸
    sizeList:'/shop/product/listSize',

    //样式列表
    styleList:'/shop/product/listStyle',


};

function specialCodeHandle(vm, res, callback) { // 服务器端特殊的返回code统一处理
    if (res.data.status == 403) { //403表示登录过期，需要重新登录
        localStorage.removeItem('user');
        vm.$router.push('/login');
        return false;
    }
    if (callback) {
        callback(res);
        return;
    }
    if (vm.logining) {
        vm.logining = false
    }
    if (vm.tableLoading) {
        vm.tableLoading = false
    }
    if (vm.formLoading) {
        vm.formLoading = false
    }
    if (vm.showLoading) {
        vm.showLoading = false
    }
    if (res.data.error.length > 20) {
        Notification.error({
            title: '错误',
            message: res.data.error,
            duration: 0,
            offset: 150
        });
    } else {
        vm.$message.error(res.data.error); //不是特殊的code，显示返回的错误信息
    }
}

function httpErrorHandle(vm, res) { // 处理http的错误状态
    if (vm.logining) {
        vm.logining = false
    }
    if (vm.tableLoading) {
        vm.tableLoading = false
    }
    if (vm.formLoading) {
        vm.formLoading = false
    }
    if (vm.showLoading) {
        vm.showLoading = false
    }
    Notification.error({
        title: 'HTTP请求错误',
        message: res.data,
        duration: 0,
        offset: 150
    });
}

/*
 * type：根据type获取接口地址
 * params：请求时的参数
 * vm： vue实例
 * callback：请求成功回调
 * errorBack：请求失败时回调，如果没有定义，则进行默认操作
 *
 * */
export const httpGet = (type, params, vm, callback, errorBack) => { //封装的axios的get方法
    return axios({
        method: 'get',
        url: api[type],
        params: params, // get 请求时带的参数
        headers: {
            'X-Requested-With': 'XMLHttpRequest'
        }
    }).then(
        (res) => {
            if (!res) { // 一直未返回res，进行提示
                Notification.error({
                    title: '错误',
                    message: '已经超过一分钟服务端没有任何返回',
                    duration: 0,
                    offset: 150
                });
                console.error(res)
                return false
            }
            if (res.status >= 200 && res.status < 300) { // http状态码在200到300间正常调用callback
                let resCode = res.data.status // 检测code码，对特殊定义的code进行处理
                if (resCode == 0) { //code值为0，操作成功
                    callback(res.data)
                } else {
                    specialCodeHandle(vm, res, errorBack) //处理特殊含义code值
                }
            } else {
                httpErrorHandle(vm, res) //处理http错误
            }
        }
    )
};

export const httpPost = (type, params, vm, callback, errorBack) => { //封装的axios的POST方法
    return axios({
        method: 'post',
        url: api[type],
        data: params, // post 请求时带的参数
        headers: {
            'X-Requested-With': 'XMLHttpRequest'
        }
    }).then(
        (res) => {
            if (!res) { // 一直未返回res，进行提示
                Notification.error({
                    title: '错误',
                    message: '已经超过一分钟服务端没有任何返回',
                    duration: 0,
                    offset: 150
                });
                console.error(res)
                return false
            }
            if (res.status >= 200 && res.status < 300) { // http状态码在200到300间正常调用callback
                let resCode = res.data.status; // 检测code码，对特殊定义的code进行处理
                if (resCode == 0) { //code值为0，操作成功
                    callback(res.data)
                } else {
                    specialCodeHandle(vm, res, errorBack);
                }
            } else {
                httpErrorHandle(vm, res) //处理http错误
            }
        }
    )
};

