import Vue from 'vue'
import Router from 'vue-router'

import Home from '../components/common/Home.vue'

Vue.use(Router)

export default new Router({
    routes: [
        /*{
         path: '/',
         redirect: '/login'
         }, */
        {
            path: '/404', //404页
            component: resolve => require(['../components/page/404.vue'], resolve),
            name: '',
            hidden: true
        }, {
            path: '/login', //登录界面
            component: resolve => require(['../components/page/login.vue'], resolve),
            name: '',
            hidden: true
        }, {
            path: '/',
            component: Home,
            name: '商品管理',
            children: [{
                path: '/product',
                component: resolve => require(['../components/page/product/product.vue'], resolve),
                name: '商品列表'
            }]
        }, {
            path: '/',
            component: Home,
            name: '货架管理',
            children: [{
                path: '/recommend',
                component: resolve => require(['../components/page/shelf/recommend.vue'], resolve),
                name: '热门推荐'
            }, {
                path: '/presale',
                component: resolve => require(['../components/page/shelf/presale.vue'], resolve),
                name: '火热预售'
            }]
        }, {
            path: '/',
            component: Home,
            name: '商家管理',
            children: [{
                path: '/business',
                component: resolve => require(['../components/page/business/business.vue'], resolve),
                name: '商家列表'
            }]
        }, {
            path: '/',
            component: Home,
            name: '订单管理',
            children: [{
                path: '/order',
                component: resolve => require(['../components/page/order/order.vue'], resolve),
                name: '订单列表'
            }]
        }, {
            path: '*',
            hidden: true,
            redirect: {
                path: '/404'
            }
        }
    ]
})



